let apiUser = function () {
    return new Promise((resolve, reject) => {
        setTimeout(() => {
            resolve({
                name: '王佳欣',
                age: 20
            });
        }, 1000);
    });
};

function * getUser () {
    let user = yield apiUser();
    console.log(user);
}

let userIterator = getUser();
userIterator.next().value.then((value) => {
    userIterator.next(value);
});


let obj = Object.assign({}, {a: 10}, {b: 20});


const s = new Set();

[2, 3, 5, 4, 5, 2, 2].forEach(x => s.add(x));

for (let i of s) {
    console.log(i);
}


